import React, { useRef, useEffect } from 'react';
import { Box } from '../Box';
import ReactPlayer from 'react-player/youtube'
// import {  findParentByMatchedQuery } from '../../core';



const SlideSubtitle = ({subtitle}) => {
    if(typeof subtitle === 'string') {
        return <Box>{subtitle}</Box>
    }
    else {
        return subtitle;
    }
}

const SlideIamge =  ({imageSrc, ...rest}) => {

    return <Box
        __css={{
            height: '100%',
            width: '100%',
            objectFit: 'cover'
        }}
        as="img"
        src={imageSrc} 
        {...rest} />
}

const SlideVideo = ({videoUrl, sx, variant, ...rest}) => {
    return <Box __css={{
        width: '100%',
        height: '100%',
        background: '0 0',
        position: 'absolute',
        top: 0,
        left: 0,
        bottom: 0,
        overflow: 'hidden'
    }}
    variant={variant}
    sx={variant}
    >
        <ReactPlayer {...rest} url={videoUrl} playing={true} loop	width="100%" height="100%" />
    </Box>
}


export const Slide = ({ children, subtitle = null, content= null, bgImage = null, bgVideoUrl = null, ...rest }) =>{
    
    const ref = useRef(null);

    useEffect(() => {

        // find visible and active parent element of this slide;
        // let el = findParentByMatchedQuery(ref.current, 'slick-slide');
        // if(el.classList && !el.classList.contains('slick-cloned')) {
        // }
    })

    // findVisibleParent()
    
    return <Box ref={ref} __css={{
    minHeight: '1px',
    height: ['300px', null, '500px'],
    width: '100%',
    display: 'flex !important',
    position: 'relative',
    '&:before': {
        content: '""',
        display: 'block',
        position: 'absolute',
        top:0,
        left: 0,
        width: '100%',
        height: '100%',
        backgroundColor: 'rgba(0,0,0,.25)',
    }
  }} {...rest}>

      {bgVideoUrl && <SlideVideo videoUrl={bgVideoUrl} />}
      {subtitle && <SlideSubtitle subtitle={subtitle} />}
      {bgImage && <SlideIamge imageSrc={bgImage} />}
      {content &&  <Box sx={{
          position: 'absolute',
          width: '100%',
          height: '100%',
          top: 0,
          left: 0,
      }}>
        {content}
      </Box> }
     
      
  </Box>}